.results-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 5% 10%;

    .error-img {
        margin-top: 20px;
        height: 380px;
        width: 380px;
    }

    h1 {
        margin: 25px 0;
    }
}

.card {
    display: flex;
    margin-top: 25px;
    justify-content: space-between;
    height: 13em;

    .card-img-wrap {
        flex-basis: 20%;

        img {
            object-fit: contain;
            width: 100%;
            height: 100%;
        }
    }

    .card-content {
        flex-basis: 76%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-between;

        .title-link {
            color: black;
            text-decoration: none;
        }

        .title-link:hover {
            color: rgb(0, 89, 255);
        }

        button {
            padding: 0 20px;
        }
    }
}

.card-container {
    display: flex;
    flex-direction: column;

    .review {
        margin-top: 25px;

        .stars-container {
            display: flex;
            margin: 15px 0;

            svg path:hover {
                fill: yellow;
                stroke: #000000;
                stroke-width: 28;
            }
        }

        .notes-form {
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            width: 60%;

            textarea {
                width: 100%;
            }

            .notes-btn-wrap {
                display: flex;
                width: 40%;

                button {
                    margin: 10px;
                    flex-grow: 1;
                }
            }
        }
    }
}
